import Vue from 'vue';
import Router from 'vue-router';
import Home from './views/Home.vue';
import Profile from './views/Profile.vue';
import Standings from './views/Standings.vue';
import StandingsTable from './views/StandingsTable.vue';
import CreateGroup from './views/CreateGroup.vue';
import SportSelect from './views/SportSelect.vue';
import SearchGroup from './views/SearchGroup.vue';
import Sport from './views/Sport.vue';
import LoginError from './views/LoginError.vue';
import Register from './views/Register.vue';
import LoginCallback from './views/LoginCallback.vue';

Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      name: 'gameSelect',
      component: SportSelect,
    },
    {
      path: '/callback/:token',
      component: LoginCallback,
    },
    {
      path: '/register',
      component: Register,
    },
    {
      path: '/errors',
      component: LoginError,
    },
    {
      path: '/:sportName',
      component: Sport,
      children: [
        {
          path: '',
          component: Home,
          name: 'home',
        },
        {
          path: 'standings',
          component: Standings,
          children: [
            {
              path: '',
              component: StandingsTable,
            },
            {
              path: 'overall',
              component: StandingsTable,
              name: 'overall',
            },
            {
              path: 'groups/:groupId',
              component: StandingsTable,
              name: 'groups',
            },
            {
              path: 'create',
              component: CreateGroup,
              name: 'create',
            },
            {
              path: 'find',
              component: SearchGroup,
              name: 'search',
            },
          ],
        },
        {
          path: 'profile/:profileId',
          name: 'profile',
          component: Profile,
        },
      ],
    },
  ],
});
